import * as React from "react";

import "./styles.scss";

const Arrow = (props) => {
    const {color = ''} = props; 
    return (
        <div aria-label="Arrow Button" role="button"
            className={`Arrow ${props.isLeft ? 'LeftArrow' : 'RightArrow'} ${color}`}>
        </div>
    );
}

export default Arrow;