import React, {useEffect, useState}  from "react";

import { useSnapCarousel } from 'react-snap-carousel';
import IconButton from "../IconButton";
import Arrow from "../../../icons/arrow";
import "./styles.scss";

const Chevron = (props) => {
  const { isLeft, onClick } = props;
  const cn = isLeft ? "ScrollButton Left" : "ScrollButton Right"; 

  return (
    <IconButton
      className={cn}
      variant="unstyled"
      onClick={onClick}
    >
      <Arrow isLeft={isLeft} color={'pink'} />
    </IconButton>
  );
}

const Carousel = (props) => {
  const [isClient, setIsClient] = useState(false);
  
  useEffect(() => {
    setIsClient(true);
  }, [setIsClient]);

  const {
    activePageIndex,
    scrollRef,
    goTo,
    snapPointIndexes,
    next,
    prev,
    pages,
  } = useSnapCarousel();
  const isAtEnd = activePageIndex === pages.length - 1;
  const isAtBeginning = activePageIndex === 0;

  const onClick = (isLeft) => {
    return () => {
      if (isLeft) {
        if (isAtBeginning) {
          goTo(pages.length - 1);
        } else {
          prev();
        }
      } else {
        if (isAtEnd) {
          goTo(0);
        } else {
          next();
        }
      }
    }
  };

  const imagesPerPage = pages.length > 0 ? pages[0].length : 0,
    start = Math.max((activePageIndex * imagesPerPage) - imagesPerPage, 0),
    end = Math.min(start + imagesPerPage + imagesPerPage, props.children.length - 1);

  return (
    <div className="Carousel">
      <ul
        className="CarouselContainer"
        ref={scrollRef}
        style={{
          display: 'flex',
          overflow: 'auto',
          scrollSnapType: 'x mandatory'
        }}
      >
        {props.children.map((childImg, idx) => {
          return (
            <li className="CarouselImage"
              style={isClient ? {
                scrollSnapAlign: snapPointIndexes.has(idx) ? "start" : ""
              } : null} key={idx}>
                {idx >= start && idx <= end &&
                  <>
                    {childImg}
                  </>
                }
            </li>
          );
        })}
      </ul>

      <Chevron 
          isLeft={true} 
          onClick={onClick(true)}
      />
      <Chevron
        isLeft={false}
        onClick={onClick(false)}
      />
    </div>
  );
};

const CarouselWrapper = (props) => {
  if (props.children.length === 0) {
    return null;
  }

  return <Carousel {...props} />
};

export default CarouselWrapper;