import React from "react";
import MainSectionWrapper from "../../index";
import AddToCartButton from "../../components/AddToCartButton";
import { GatsbyImage } from "gatsby-plugin-image";
import Carousel from "../../components/Carousel";
import SelectDropdown from "../../components/Select";

import { Heading, Text, Stack } from '@chakra-ui/react';

import "./styles.scss";

const ShippingDisclaimer = () => {
    return (
        <Stack>
            <Heading as='h4' size="md">Shipping</Heading>
            <Text>Made to order, will ship in 1 - 2 weeks.</Text>
            <Text>Note: Each item is made with highest care and attention to details. However due to the nature of handmade items, slight differences may show from the pictures.</Text>
            <Text>Ships from California!</Text>
            <Text><i>Gentle use is recommended</i></Text>
        </Stack>
    );
}

const ProductDetailsImages = (props) => {
    const { photos, title } = props;

    return (
        <div className="ProductDetailsPhotoCarousel">
            <Carousel>
                {photos.map(photo => {
                    return (
                        <div className="ProductDetailsSecondaryPhoto" key={photo}>
                            <GatsbyImage
                                key={photo}
                                image={photo}
                                alt={title}
                            />
                        </div>
                    );
                })}
            </Carousel>
        </div>
    );
}

const ProductDetails = (props) => {
    const {
        title,
        description,
        price,
        variations,
        etsyAffiliateLink,
    } = props;

    return (
        <div className="ProductDetailsProductDetails">
            <Stack className="ProductDetailsPricingDetails">
                <Heading as='h1' size="lg">{title}</Heading>
                <Heading as='h2' size="md" variant="nonBoldHeading">${parseFloat(price).toFixed(2)}+ USD</Heading>

                <AddToCartButton etsyAffiliateLink={etsyAffiliateLink} />

                <Heading as='h3' size="md">Item details</Heading>
                <Text as='p' variant="nonBoldHeading" className="ProductDescription">
                    {description}
                </Text>

                {false && variations.map((variation) => {
                    return (
                        <SelectDropdown 
                            key={variation.name} 
                            placeholder={variation.name}
                            values={variation.values}
                        />
                    );
                })}

            </Stack>

            <Stack>

                <ShippingDisclaimer />
            </Stack>
        </div>
    );
}

const ProductDetailsPageContainer = (props) => {
    const {
        title,
        gatsbyImages,
    } = props;

    return (
        <div className="ProductDetailsPageContainer">
            <ProductDetailsImages photos={gatsbyImages} title={title} />
            <ProductDetails {...props} />
        </div>
    );
}

const ProductDetailsPage = (props) => {
    const { product } = props;
    return (
        <>
            <MainSectionWrapper>
                <ProductDetailsPageContainer 
                    {...product}
                />
            </MainSectionWrapper>
        </>
    )
};

export default ProductDetailsPage;