import React, { useContext, useState }  from "react";
import cartContext from "../../../context/cartContext";
import { navigate } from "gatsby";

import { Button } from '@chakra-ui/react';
import EtsyLink from "../EtsyLink";

const AddToCartButton = (props) => {
    const [ isAdding, setIsAdding] = useState(false);
    const { cart, updateQuantity } = useContext(cartContext);

    const {id, price, priceId, etsyAffiliateLink, shouldRedirectToCart } = props;
    const addItem = () => {
        const item = cart.find(item => item.priceId === priceId);
        if (!item) {
            updateQuantity(priceId, id, 1);
        } else {
            updateQuantity(priceId, id, item.quantity + 1);
        }
        setIsAdding(true);
        setTimeout(() => {
            setIsAdding(false);
            if (shouldRedirectToCart) {
                navigate("/cart")
            }
        }, 400);
    };

    const addToCartText = price ? `Add to cart | $${price}` : 'Add to cart';
    const text = isAdding ? 'Adding' : addToCartText;
    return (
        <>
            {false &&<Button variant={'pink'} disabled={isAdding} onClick={addItem}>{text}</Button>}
            <EtsyLink etsyLink={etsyAffiliateLink} />
        </>
    )
}

export default AddToCartButton;